import { Box, Heading, HStack, Text, VStack } from "@chakra-ui/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleRight,
  faExternalLink
} from "@fortawesome/free-solid-svg-icons";
import React from "react";

const Card = ({ title, description, skills, link, timePeriod }) => {
  return (
    <VStack color="black" backgroundColor="white" cursor="pointer">
      <Box>
        <VStack spacing={4} alignItems="flex-start" p={5}>
          {link ? (
            <a href={link} target="_blank" rel="noopener noreferrer">
              <Heading as="h4" size="md">
                {title} &nbsp;
                <FontAwesomeIcon icon={faExternalLink} size="sm" />
              </Heading>
            </a>
          ) : (
            <Heading as="h4" size="md">
              {title}
            </Heading>
          )}
          <Text fontWeight="bold">{timePeriod}</Text>
          <Text>
            {description.split("\n").map((line, i) => (
              <React.Fragment key={i}>
                <Text color="black">
                  <FontAwesomeIcon icon={faAngleRight} size="sm" /> &nbsp;
                  {line}
                </Text>
              </React.Fragment>
            ))}
          </Text>
          {skills && (
            <HStack wrap="wrap" spacing={2}>
              {skills.split(",").map((individualSkill, j) => (
                <Box
                  key={j}
                  color="white"
                  backgroundColor={
                    individualSkill.includes(":") ? "black" : "grey"
                  }
                  p={1.5}
                  borderRadius="5px"
                >
                  {individualSkill.trim()}
                </Box>
              ))}
            </HStack>
          )}
        </VStack>
      </Box>
    </VStack>
  );
};

export default Card;
