import React from "react";
import FullScreenSection from "./FullScreenSection";
import { Box, Heading, HStack, Image, Text } from "@chakra-ui/react";
import Card from "./Card";

const experiences = [
  {
    title: "Associate Software Engineer - ACS Solutions",
    link: "https://acsicorp.com/about-acs/",
    company: "ACS Solutions",
    timePeriod: "Aug 2021 - Nov 2022",
    description:
      "Developed and implemented features to enhance reimbursement accuracy for insurance users, leading to a 50% reduction in reimbursement errors, resulting in cost savings for insurance companies.\nDesigned and deployed location-specific modules for seamless user matching with nearby hospitals, enhancing healthcare accessibility by adapting to changing project dynamics.\nCreated real-time reimbursement tracking tools, reduced processing time by 60%, enhanced user experience, and maintained timelines for faster project delivery using Agile methodology by conducting regular code analysis.\nDemonstrated strong problem-solving skills, proactively addressing customer concerns and ensuring project success.",
    getImageSrc: () => require("../images/ACS_logo.jpeg")
  },
  {
    title: "Software Trainee - ACS Solutions",
    link: "https://acsicorp.com/about-acs/",
    company: "ACS Solutions",
    timePeriod: "Feb 2021 - July 2021",
    description:
      "As a Software Trainee, I underwent training in back-end technologies, focusing on Spring Boot and SQL, and also received instruction in front-end technologies, specializing in UI/UX design. Furthermore, I acquired a fundamental grasp of Kafka during my training. \nShowcased commitment to continous learning by actively participating in training sessions",
    getImageSrc: () => require("../images/ACS_logo.jpeg")
  }
];

const ExperienceSection = () => {
  return (
    <FullScreenSection
      backgroundColor="#ffffff"
      isDarkBackground
      p={8}
      alignItems="flex-start"
      spacing={8}
    >
      <Heading id="experience-section"></Heading>
      <Heading as="h1" color="black">
        Experience
      </Heading>
      <Box
        display="grid"
        gridTemplateColumns="repeat(1,minmax(0,1fr))"
        gridGap={8}
      >
        {experiences.map((experience) => (
          <Box p={4} shadow="lg" borderWidth="1px">
            <HStack spacing={2}>
              <Box maxWidth="500px">
                <Image
                  alt={experience.title}
                  src={experience.getImageSrc()}
                  size="1x"
                />
              </Box>
              {experience.title.includes("Software Trainee ") && (
                <Heading></Heading>
              )}
              <Card
                key={experience.title}
                title={experience.title}
                description={experience.description}
                timePeriod={experience.timePeriod}
                link={experience.link}
              />
            </HStack>
          </Box>
        ))}
      </Box>
    </FullScreenSection>
  );
};

export default ExperienceSection;
