import React from "react";
import { Box, Button, Heading, HStack, Image, VStack } from "@chakra-ui/react";
import FullScreenSection from "./FullScreenSection";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleChevronDown } from "@fortawesome/free-solid-svg-icons";

const greeting = "Hello, I'm Swetha Uppula!";
const bio1 =
  "An accomplished Full Stack Developer with 2 years of professional expertise and an ongoing pursuit of a Master's degree in Information Systems from the University of Utah. Proficient in crafting resilient and user-centric web applications through a wide array of technologies and frameworks. Adept at seamless cross-functional teamwork, dedicated to ongoing learning, and fostering innovation for outstanding results.";
const bio2 =
  "I love designing and coding user-centric solutions with a creative approach to problem-solving.";
const bio3 = "Learn more about me";

const handleClick = (anchor) => () => {
  const id = `${anchor}-section`;
  const element = document.getElementById(id);
  if (element) {
    element.scrollIntoView({
      behavior: "smooth",
      block: "start"
    });
  }
};

const LandingSection = () => (
  <FullScreenSection
    justifyContent="center"
    alignItems="center"
    isDarkBackground
    backgroundColor="black"
    backgroundImage={require("../images/background_portfolio.jpeg")}
    backgroundSize="cover"
    backgroundPosition="center"
    minWidth="99vw"
    minHeight="100vh"
    id="home"
  >
    <VStack spacing={24}>
      <br />
      <br />
      <br />
      <br />
      <br />
      <br />
      <HStack spacing={8}>
        <VStack spacing={10}>
          <Heading as="h1" size="2xl">
            {greeting}
          </Heading>
          <Box paddingLeft="70px" paddingRight="50px">
            <p>{bio2}</p>
          </Box>
          <Button
            color="black"
            backgroundColor="white"
            _hover={{ backgroundColor: "grey", color: "black" }}
          >
            <a
              key="projects"
              href="#projects-section"
              onClick={handleClick("projects")}
            >
              Projects
            </a>
          </Button>
        </VStack>
      </HStack>
      <br />
      <a key="about" href="#about" onclick={handleClick("about")}>
        <VStack spacing={4}>
          <Box paddingLeft="70px" paddingRight="50px">
            <p>{bio3}</p>
          </Box>
          <FontAwesomeIcon icon={faCircleChevronDown} size="2xl" />
        </VStack>
      </a>
      {/* <br /> */}
    </VStack>
  </FullScreenSection>
);

export default LandingSection;
