import React from "react";
import FullScreenSection from "./FullScreenSection";
import { Box, Heading, HStack, Image, Text } from "@chakra-ui/react";
import Card from "./Card";

const certificates = [
  {
    title: "Advanced React",
    link:
      "https://www.coursera.org/account/accomplishments/certificate/LFZ4ER57GDU5",
    timePeriod: "Meta",
    description:
      "Earned Advanced React certification, specializing in React components, advanced hooks, form building, and integration with various tools. \nCapable of rendering lists, leveraging React Context, and implementing custom hooks.",
    getImageSrc: () => require("../images/meta.png")
  },
  {
    title: "Oracle Certified Associate, Java SE 8 Programmer",
    link:
      "https://drive.google.com/file/d/1CeUqbnlxwB84YY8ZFsmf7O6LQxzx_ODO/view?usp=sharing",
    timePeriod: "Oracle University",
    description:
      "Certified in Java SE 8 programming, covering fundamental concepts and object-oriented development. \nProficient in creating efficient Java applications and handling exceptions effectively",
    getImageSrc: () => require("../images/oracle.png")
  }
];

const CertificateSection = () => {
  return (
    <FullScreenSection
      backgroundColor="#ffffff"
      isDarkBackground
      p={8}
      alignItems="flex-start"
      spacing={8}
    >
      <Heading as="h1" id="certificate-section"></Heading>
      <Heading as="h1" color="black">
        Certifications
      </Heading>
      <Box
        display="grid"
        gridTemplateColumns="repeat(2,minmax(0,1fr))"
        gridGap={8}
      >
        {certificates.map((certificate) => (
          <Box p={4} shadow="lg" borderWidth="1px">
            <HStack spacing={2}>
              <Box maxWidth="300px">
                <Image
                  alt={certificate.title}
                  src={certificate.getImageSrc()}
                  size="1x"
                />
              </Box>
              <Card
                key={certificate.title}
                title={certificate.title}
                description={certificate.description}
                timePeriod={certificate.timePeriod}
                link={certificate.link}
              />
            </HStack>
          </Box>
        ))}
      </Box>
    </FullScreenSection>
  );
};

export default CertificateSection;
