import React from "react";
import FullScreenSection from "./FullScreenSection";
import { Box, Heading, HStack, Image, Text } from "@chakra-ui/react";
import Card from "./Card";

const education_list = [
  {
    title: "University of Utah",
    link: "https://www.utah.edu/",
    timePeriod: "2023 - present",
    description:
      "My current academic journey at the University of Utah has been focused on a comprehensive exploration of web development. This journey encompasses a deep dive into front-end and back-end technologies, database management, and the intricacies of data analysis. \nAlong this path, I've honed my expertise in a wide array of tools and technologies, including Bootstrap, PHP, Python, PostgreSQL, SQLite, RStudio, PowerBI, and Domo.\n As part of my studies, I've had the opportunity to apply this knowledge in practical project work, solidifying my skills in real-world scenarios.",
    getImageSrc: () => require("../images/univ_utah.png")
  },
  {
    title: "Keshav Memorial Institute of Technology",
    link: "https://www.kmit.in/",
    timePeriod: "2016 - 2020",
    description:
      "During my undergraduate years at Keshav Memorial Institute of Technology, I laid a strong foundation in programming, gaining expertise in languages like Java, Python, and C. I also developed skills in web technologies, including HTML, CSS, and PHP. \nOver the course of my studies, I actively participated in project-based learning experiences, honing my problem-solving and teamwork abilities.",
    getImageSrc: () => require("../images/kmit_logo_1.jpeg")
  }
];

const EducationSection = () => {
  return (
    <FullScreenSection
      backgroundColor="#ffffff"
      isDarkBackground
      p={8}
      alignItems="flex-start"
      spacing={8}
    >
      <Heading as="h1" id="education-section"></Heading>
      <Heading as="h1" color="black">
        Education
      </Heading>
      <Box
        display="grid"
        gridTemplateColumns="repeat(1,minmax(0,1fr))"
        gridGap={8}
      >
        {education_list.map((education) => (
          <Box p={4} shadow="lg" borderWidth="1px">
            <HStack spacing={2}>
              <Box maxWidth="300px">
                <Image
                  alt={education.title}
                  src={education.getImageSrc()}
                  size="1x"
                />
              </Box>
              <Card
                key={education.title}
                title={education.title}
                description={education.description}
                timePeriod={education.timePeriod}
                link={education.link}
              />
            </HStack>
          </Box>
        ))}
      </Box>
    </FullScreenSection>
  );
};

export default EducationSection;
