import React, { useEffect, useRef } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope } from "@fortawesome/free-solid-svg-icons";
import {
  faGithub,
  faGitlab,
  faLinkedin
} from "@fortawesome/free-brands-svg-icons";
import { Box, HStack, Image } from "@chakra-ui/react";

const socials = [
  {
    icon: faEnvelope,
    url: "mailto: swethauppulak@gmail.com"
  },
  {
    icon: faGithub,
    url: "https://github.com/Swetha-Uppula"
  },
  {
    icon: faGitlab,
    url: "https://gitlab.com/users/Swetha_Uppula/contributed"
  },
  {
    icon: faLinkedin,
    url: "https://www.linkedin.com/in/swetha-uppula-16492b19b/"
  }
];

const Header = () => {
  const handleClick = (anchor) => () => {
    const id = `${anchor}-section`;
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({
        behavior: "smooth",
        block: "start"
      });
    }
  };

  const headerElementRef = useRef(null);

  useEffect(() => {
    let previousScrollPosition = window.scrollY;
    const handleScroll = () => {
      const currentScrollPosition = window.scrollY;
      const currentHeaderElement = headerElementRef.current;

      if (!currentHeaderElement) return;

      if (previousScrollPosition > currentScrollPosition)
        currentHeaderElement.style.transform = "translateY(0)";
      else currentHeaderElement.style.transform = "translateY(-200px)";

      previousScrollPosition = currentScrollPosition;
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <Box
      position="fixed"
      top={0}
      left={0}
      right={0}
      translateY={0}
      transitionProperty="transform"
      transitionDuration=".3s"
      transitionTimingFunction="ease-in-out"
      backgroundColor="#18181b"
    >
      <Box color="white" maxWidth="1280px" margin="0 auto">
        <HStack
          px={16}
          py={4}
          justifyContent="space-between"
          alignItems="center"
        >
          <nav>
            <HStack>
              {/* <Image
                src={require("../images/ACS_logo.jpeg")}
                style={{ borderRadius: "50%", width: "40px", height: "40px" }}
              /> */}
              <h1>
                <a key="home" href="#home" onclick={handleClick("home")}>
                  <strong> SWETHA UPPULA </strong>
                </a>
              </h1>
            </HStack>
          </nav>
          <nav>
            <HStack spacing="120px">
              <HStack spacing={6}>
                {/* Add links to Projects and Contact me section */}
                <a key="home" href="#home" onclick={handleClick("home")}>
                  Home
                </a>
                <a key="about" href="#about" onclick={handleClick("about")}>
                  About
                </a>
                <a
                  key="experience"
                  href="#experience-section"
                  onclick={handleClick("experience")}
                >
                  Experience
                </a>
                <a
                  key="education"
                  href="#education-section"
                  onclick={handleClick("education")}
                >
                  Education
                </a>
                <a
                  key="certifications"
                  href="#certificate-section"
                  onClick={handleClick("certifications")}
                >
                  Certifications
                </a>
                <a
                  key="projects"
                  href="#projects-section"
                  onClick={handleClick("projects")}
                >
                  Projects
                </a>
                <a
                  key="contact-me"
                  href="#contactme-section"
                  onClick={handleClick("contact-me")}
                >
                  Contact Me
                </a>
              </HStack>
              <HStack spacing={6}>
                {socials.map((social, index) => (
                  <a
                    href={social.url}
                    key={index}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    <FontAwesomeIcon icon={social.icon} size="lg" />
                  </a>
                ))}
              </HStack>
            </HStack>
          </nav>
        </HStack>
      </Box>
    </Box>
  );
};
export default Header;
