import React from "react";
import { Box, Center, Heading, HStack, Text, VStack } from "@chakra-ui/react";
import FullScreenSection from "./FullScreenSection";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEnvelope,
  faCircleChevronUp
} from "@fortawesome/free-solid-svg-icons";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";

const socials = [
  {
    icon: faEnvelope,
    url: "mailto: swethauppulak@gmail.com",
    description: "Email me at",
    link: "swethauppulak@gmail.com"
  },
  {
    icon: faLinkedin,
    url: "https://www.linkedin.com/in/swetha-uppula-16492b19b/",
    description: "Let's Connect!",
    link: "LinkedIn Profile"
  },
  {
    icon: faGithub,
    url: "https://github.com/Swetha-Uppula",
    description: "Explore my code and projects",
    link: "GitHub Profile"
  }
];

const handleClick = (anchor) => () => {
  const id = `${anchor}-section`;
  const element = document.getElementById(id);
  if (element) {
    element.scrollIntoView({
      behavior: "smooth",
      block: "start"
    });
  }
};

const ContactMeSection = () => {
  return (
    <FullScreenSection
      isDarkBackground
      backgroundColor="#000000"
      py={16}
      spacing={8}
      minHeight="60vh"
    >
      <VStack spacing={2} alignItems="center">
        <Text>Contact me</Text>
        <Heading as="h3" id="contactme-section" color="Orange">
          I'd love to connect with You!
        </Heading>
        <Text color="#64748b" fontSize="lg">
          Let me get to know more about you.
        </Text>
        <br />
        <br />
        <br />
        <HStack spacing={64}>
          {socials.map((social, index) => (
            <Center flexDirection="column" key={index}>
              <VStack spacing={8} alignItems="center">
                <a
                  href={social.url}
                  key={index}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <Center flexDirection="column">
                    <FontAwesomeIcon
                      icon={social.icon}
                      size="xl"
                      color="white"
                      style={{ borderRadius: "50%" }}
                    />
                    <br />
                    <VStack>
                      <Text color="Orange">{social.description}</Text>
                      <Text color="lightblue">{social.link}</Text>
                    </VStack>
                  </Center>
                </a>
              </VStack>
            </Center>
          ))}
        </HStack>
        <br />
        <br />
        <br />
        <br />
        <br />
        <br />
        <a key="home" href="#home" onclick={handleClick("home")}>
          <VStack spacing={4}>
            <Box paddingLeft="70px" paddingRight="50px">
              <p>Back to the top</p>
            </Box>
            <FontAwesomeIcon icon={faCircleChevronUp} size="xl" />
          </VStack>
        </a>
      </VStack>
    </FullScreenSection>
  );
};

export default ContactMeSection;
