import { Box, Heading, HStack, Text, VStack } from "@chakra-ui/react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";

const SkillCard = ({ icon, title, description, skills }) => {
  const skillList = skills.split("\n").map((skill) => skill.trim());
  return (
    <VStack
      color="black"
      backgroundColor="white"
      cursor="pointer"
      borderRadius="xl"
      _hover={{ backgroundColor: "lightgray", boxShadow: "xl" }}
    >
      <Box
        p={5}
        shadow="lg"
        borderWidth="1px"
        width="440px"
        height="520px"
        borderRadius="xl"
      >
        <VStack spacing={4} alignItems="center" p={5}>
          <FontAwesomeIcon icon={icon} size="2xl" />
          <Heading as="h4" size="md" color="red">
            {title}
          </Heading>
          <Text color="black" fontSize="lg">
            {description}
          </Text>
          <HStack wrap="wrap">
            {skillList.map((skill, i) => (
              <Box key={i} color="black">
                <HStack wrap="wrap" spacing={2}>
                  {skill.split(",").map((individualSkill, j) => (
                    <Box
                      key={j}
                      color="white"
                      backgroundColor={
                        individualSkill.includes(":") ? "black" : "grey"
                      }
                      p={1.5}
                      borderRadius="5px"
                    >
                      {individualSkill.trim().replace(":", "")}
                    </Box>
                  ))}
                </HStack>
              </Box>
            ))}
          </HStack>
        </VStack>
      </Box>
    </VStack>
  );
};

export default SkillCard;
