import React from "react";
import FullScreenSection from "./FullScreenSection";
import { Box, Heading, HStack, Image } from "@chakra-ui/react";
import Card from "./Card";

const projects = [
  {
    title: "AMS",
    description:
      "The project's primary objective was to assist insurance companies in accurately predicting reimbursements for their clients.\nDeveloped healthcare cost management modules, including bill information extraction. \nUtilized Azure for PDF storage and retrieval. \nI was responsible for the end-to-end development process in this role, including analyzing project requirements, designing and implementing solutions, debugging, integrating databases, optimizing performance, and thorough testing.\nNotably, my contributions resulted in substantial improvements, including a remarkable 50% reduction in errors, a 60% decrease in response times, and a significantly enhanced user experience.",
    skills:
      "Spring Boot, Java, MySQL, JDBC, OAuth, OpenID, Angular, HTML, CSS, TypeScript, XML, Azure, GitHub, Jira, Visual Studio, NPM, Postman",
    link: "https://www.mdstrat.com/#predict-suite",
    getImageSrc: () => require("../images/AMS.jpeg")
  },
  {
    title: "Portfolio",
    description:
      "Portfolio, a web application powered by React. It's more than just a CV; it's a visual journey through my skills, accomplishments, and a showcase of my work and projects. With expertise in the below technologies, I've brought my digital presence to life.",
    skills: "React, HTML, CSS, JavaScript",
    link: "https://github.com/Swetha-Uppula/portfolio.git",
    getImageSrc: () => require("../images/portfolio.png")
  },
  {
    title: "Recipe Creator and Manager",
    description:
      "The Recipe Creator and Manager is a user-friendly culinary solution designed to simplify and enhance the cooking experience. It serves as a digital recipe book and organizational tool, allowing users to store, categorize, and access their recipes with ease. \nUsers can create, edit, and delete recipes, organize them into predefined or custom categories, and manage ingredients for kitchen inventory and shopping lists.\nThis project aims to bridge the gap between culinary inspiration and organized execution by centralizing and categorizing recipes. Our application inspires creativity in the kitchen, promotes efficient food management, and ensures a delightful and sustainable culinary journey.",
    skills: "Python, SQLite",
    link: "https://github.com/Swetha-Uppula/python_programming.git",
    getImageSrc: () => require("../images/recipe.png")
  },
  {
    title: "USA Trauma Database (UTDB)",
    description:
      "Created a comprehensive database system to track and analyze patient treatment data following trauma incidents.\nThe UTDB uses the National Trauma Data Standard (NTDS) as the tool in which to assemble and standardize its data.\nThe mission of the UTDB has been to provide medical professionals with the most up-to-date data to provide patients with the most comprehensive and effective care. Data Save Live: The more we know, the more we share, the more patients can recover to full health.",
    skills: "PostgreSQL, Lucid Chart",
    getImageSrc: () => require("../images/trauma.png")
  },
  {
    title: "Car Rental",
    description:
      "The Car Rental System is designed for specialized car rental companies to streamline their operations. It's an online platform that allows customers to browse available cars, register, and book vehicles. \nThe project's primary goal is to automate tasks such as daily bookings, car and route records, rental charges, and customer information management within the car rental system.",
    skills: "PHP, HTML, CSS, MySQL",
    link: "https://github.com/Swetha-Uppula/car_rental_system",
    getImageSrc: () => require("../images/car.png")
  },
  {
    title: "Library Management",
    description:
      "Developed a library management application with features for book organization, user verification, and book borrowing and return processes, enhancing overall library efficiency and organization.",
    skills: "PHP, HTML, CSS, JavaScript, MySQL",
    link: "https://github.com/Swetha-Uppula/library_management_system",
    getImageSrc: () => require("../images/library.png")
  }
];

const ProjectsSection = () => {
  return (
    <FullScreenSection
      backgroundColor="#ffffff"
      isDarkBackground
      p={8}
      alignItems="flex-start"
      spacing={8}
    >
      <Heading id="projects-section"></Heading>
      <Heading as="h1" color="black">
        Projects
      </Heading>
      <Box
        display="grid"
        gridTemplateColumns="repeat(1,minmax(0,1fr))"
        gridGap={8}
      >
        {projects.map((project) => (
          <Box p={4} shadow="lg" borderWidth="1px">
            <HStack spacing={8}>
              <Box maxWidth="500px">
                <Image
                  alt={project.title}
                  src={project.getImageSrc()}
                  size="1x"
                />
              </Box>
              <Card
                key={project.title}
                title={project.title}
                description={project.description}
                skills={project.skills}
                link={project.link}
                imageSrc={project.getImageSrc()}
              />
            </HStack>
          </Box>
        ))}
      </Box>
    </FullScreenSection>
  );
};

export default ProjectsSection;
